import Logo from '../../Images/image.png';
import Leaves from '../../Images/img.png';
import { motion } from "framer-motion"

const Main = () => {
    return (
        <section>
            <motion.div
                initial={{ opacity: 0, scale: 0.5 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{
                    duration: 0.8,
                    delay: 0.3,
                    ease: [0, 0.71, 0.2, 1.01]
                }}
            >
                <div className='flex flex-row justify-between items-center mt-9 mx-7 xl:mx-40 '>
                    <img src={Logo} className="w-12 h-12 md:w-16 md:h-16 lg:w-20 lg:h-20" alt="logo" />
                    <a href="https://kirekutanovski.github.io/my-portofolio/" className='md:text-2xl'>Home</a>
                </div>
            </motion.div>
            
            <motion.div
                initial={{ opacity: 0, scale: 0.5 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{
                    duration: 0.8,
                    delay: 0.5,
                    ease: [0, 0.71, 0.2, 1.01]
                }}
            >
                <div className='flex justify-center text-center mt-40 text-3xl md:text-6xl lg:text-8xl font-black'>
                    <h1>UNLEASH. <br /> YOUR. <br />CREATIVITY.</h1>
                </div>
            </motion.div>

            <motion.div
                initial={{ opacity: 0, scale: 0.5 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{
                    duration: 0.8,
                    delay: 0.8,
                    ease: [0, 0.71, 0.2, 1.01]
                }}
            >
            <div className='flex justify-center text-center mt-5 leading-none text-sm md:text-base md:leading-none lg:text-xl lg:leading-none'>
                <p>Join our community of creative talents and <br />unlock endless possibilities for your artistic <br /> career</p>
            </div>
            </motion.div>
            
            <motion.div
                initial={{ opacity: 0, scale: 0.5 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{
                    duration: 0.8,
                    delay: 1.1,
                    ease: [0, 0.71, 0.2, 1.01]
                }}
            >
                <div className='flex justify-center mt-10 grayscale'>
                    <img src={Leaves} className="w-32 h-32 md:w-64 md:h-64 lg:w-72 lg:h-72" alt="" />
                </div>
            </motion.div>

        </section>
    );
}

export default Main;